import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FileChooser } from '@ionic-native/file-chooser';
import { ImagePicker } from '@ionic-native/image-picker';
import { File } from '@ionic-native/file';
import { Component } from '@angular/core';

import { HostProvider } from '../../providers/host/host';

@IonicPage()
@Component({
  selector: 'page-produk-form',
  templateUrl: 'produk-form.html',
})
export class ProdukFormPage {

  nama_produk: any;
  harga: any;
  deskripsi: any;
  thumbnail: any;
  imageURI: any = '';

  ikm: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private host: HostProvider, private fileChooser: FileChooser, private imagePicker: ImagePicker, private transfer: FileTransfer, private file: File) {
    this.ikm = this.navParams.get('ikm');
    console.log(this.ikm);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProdukFormPage');
  }

  selectFile(){
    const options = { maximumImagesCount: 1 };
    this.imagePicker.getPictures(options).then((results) => {
      for (var i = 0; i < results.length; i++) {
          console.log('Image URI: ' + results[i]);
          this.imageURI = results[i];
      }
    }, (err) => { console.log(err); });
  }

  submitData(){
    const fileTransfer: FileTransferObject = this.transfer.create();
    let options: FileUploadOptions = {
       fileKey: 'thumbnail',
       params: {
         nama: this.nama_produk,
         harga: this.harga,
         deskripsi: this.deskripsi,
         ikm_id: this.ikm.id
       }
    }

    fileTransfer.upload(this.imageURI, this.host.baseUrl + '/ikm/produk/store', options)
     .then((data) => {
       console.log(data);
       alert("success");
     }, (err) => {
       console.log(err);
       alert("failed");
     })
  }

}



